export default {
  name: "o-navbar",
  props: {
    theme: {
      type: String,
      default() {
        return "light";
      }
    }
  },
  computed: {
    compTheme() {
      if (this.theme === "light") {
        return ["navbar-light", "bg-light", "text-dark"].join(" ");
      } else {
        return ["navbar-dark", "bg-dark", "text-light"].join(" ");
      }
    }
  }
};